import { Component, h, Prop, Element } from '@stencil/core';

/**
 * Image is a component that displays an image.
 * It supports lazy loading of images to improve performance,
 * the image will only be loaded when it enters the viewport.
 */
@Component({
    tag: 'limel-image',
    shadow: true,
    styleUrl: 'image.scss',
})
export class Image {
    /**
     * The image source URL
     */
    @Prop()
    public src: string;

    /**
     * Alternative text for the image
     */
    @Prop()
    public alt: string = '';

    /**
     * Enable lazy loading of the image
     */
    @Prop()
    public lazyLoad = false;

    @Element()
    private host: HTMLLimelImageElement;

    private imageIntersectionObserver: IntersectionObserver | null = null;

    public componentDidLoad() {
        this.setupImageIntersectionObserver();
    }

    public disconnectedCallback() {
        this.teardownImageIntersectionObserver();
    }

    public render() {
        if (!this.src) {
            return;
        }

        const props = { alt: this.alt };

        if (this.lazyLoad) {
            props['data-src'] = this.src;
        } else {
            props['src'] = this.src;
        }

        return <img {...props} />;
    }

    private setupImageIntersectionObserver() {
        if (this.lazyLoad) {
            this.imageIntersectionObserver = new IntersectionObserver(
                this.handleImageIntersection
            );

            const images = this.host.shadowRoot.querySelectorAll('img');
            for (const img of images) {
                this.imageIntersectionObserver.observe(img);
            }
        }
    }

    private readonly handleImageIntersection = (
        entries: IntersectionObserverEntry[]
    ) => {
        for (const entry of entries) {
            if (entry.isIntersecting) {
                const img = entry.target as HTMLImageElement;
                const dataSrc = img.dataset.src;

                if (dataSrc) {
                    img.setAttribute('src', dataSrc);
                    delete img.dataset.src;
                }

                this.imageIntersectionObserver.unobserve(img);
            }
        }
    };

    private teardownImageIntersectionObserver() {
        if (this.imageIntersectionObserver) {
            this.imageIntersectionObserver.disconnect();
            this.imageIntersectionObserver = null;
        }
    }
}
